___CSS_LOADER_EXPORT___.push([module.id, ".modal-content{position:absolute!important;width:100%!important}.mostrar{background-color:#3c29297a!important;display:list-item!important;opacity:1!important;overflow:scroll;position:fixed!important}.div-error{display:flex;justify-content:center}.text-error{color:red!important;font-weight:700}", "",{"version":3,"sources":["webpack://./resources/js/components/superadmin/PreRegisterComponent.vue"],"names":[],"mappings":"AAwMI,eAEI,2BAA6B,CAD7B,oBAEJ,CACA,SAII,oCAAsC,CAHtC,2BAA6B,CAC7B,mBAAqB,CAGrB,eAAgB,CAFhB,wBAGJ,CAEA,WACI,YAAa,CACb,sBACJ,CAEA,YACI,mBAAqB,CACrB,eACJ","sourcesContent":["<template>\n  <div>\n\n  <div class=\"container-fluid\">\n\n        <!-- Ejemplo de tabla Listado -->\n        <div class=\"card\">\n            <div class=\"card-header\">\n                <i class=\"fa fa-align-justify\"></i> Pre Registros\n            </div>\n            <div class=\"card-body\">\n                <div class=\"form-group row\">\n                    <div class=\"col-md-6\">\n                        <div class=\"input-group\">\n                            <select class=\"form-control col-md-3\" v-model=\"criterio\">\n                              <option value=\"name\">Nombre</option>\n                              <option value=\"description\">Descripcin</option>\n                            </select>\n                            <input type=\"text\" v-model=\"buscar\" class=\"form-control\" placeholder=\"Texto a buscar\" @keyup.enter=\"loadRegistros(1,buscar,criterio)\">\n                            <button type=\"submit\" @click=\"loadRegistros(1,buscar,criterio)\" class=\"btn btn-primary\"><i class=\"fa fa-search\"></i> Buscar</button>\n                        </div>\n                    </div>\n                </div>\n                <table class=\"table table-bordered table-striped table-sm\">\n                    <thead>\n                        <tr>\n                            <th>No</th>\n                            <th>Confirmado</th>\n                            <th>Nombre</th>\n                            <th>Tel.</th>\n                            <th>Email</th>\n                            <th>Obs.</th>\n                            <th>Creacin</th>\n                            <th>Opciones</th>\n                        </tr>\n                    </thead>\n                    <tbody>\n                          <tr v-for=\"reg in arrayRegistros\" :key=\"reg.id\">\n                              <td v-text=\"reg.id\"></td>\n                              <td>\n                                  <span v-if=\"reg.confirmed == 0\" class=\"badge bg-danger\">NO</span>\n                                  <span v-else class=\"badge bg-success\">SI</span>\n                              </td>\n                              <td v-text=\"reg.name\"></td>\n                              <td v-text=\"reg.phone\"></td>\n                              <td v-text=\"reg.email\"></td>\n                              <td v-text=\"reg.observations\"></td>\n                              <td>{{reg.created_at | formatDate }}</td>\n                              <td>\n                                <button v-if=\"reg.confirmed == 0\" @click=\"eliminar(reg.id)\" class=\"btn btn-sm btn-danger\">\n                                    Eliminar\n                                </button>\n                              </td>\n                          </tr>\n                  </tbody>\n                </table>\n                <nav>\n                    <ul class=\"pagination\">\n                        <li class=\"page-item\" v-if=\"pagination.current_page > 1\">\n                            <a class=\"page-link\" href=\"#\" @click.prevent=\"cambiarPagina(pagination.current_page-1,buscar,criterio)\">Ant</a>\n                        </li>\n\n                        <li class=\"page-item\" v-for=\"page in pagesNumber\" :key=\"page\" :class=\"[page==isActived ? 'active':'']\">\n                            <a class=\"page-link\" href=\"#\" @click.prevent=\"cambiarPagina(page,buscar,criterio)\" v-text=\"page\"></a>\n                        </li>\n\n                        <li class=\"page-item\" v-if=\"pagination.current_page < pagination.last_page\">\n                            <a class=\"page-link\" href=\"#\" @click.prevent=\"cambiarPagina(pagination.current_page+1,buscar,criterio)\">Sig</a>\n                        </li>\n                    </ul>\n                </nav>\n            </div>\n        </div>\n        <!-- Fin ejemplo de tabla Listado -->\n    </div>\n\n</div>\n</template>\n\n<script>\n    export default {\n        data(){\n            return {\n              arrayRegistros:[],\n              pagination:{\n                  'total':0,\n                  'current_page':0,\n                  'per_page':0,\n                  'last_page':0,\n                  'from':0,\n                  'to':0\n              },\n              offset:3,\n              criterio:'name',\n              buscar:'',\n\n              request_id:0,\n              confirmed:0,\n              token:'',\n              name:'',\n              phone:'',\n              email:'',\n              observations:'',\n\n            }\n        },\n        computed:{\n           isActived: function(){\n            return this.pagination.current_page;\n           },\n           //Calcula los elementos de la paginacion\n           pagesNumber: function(){\n                if(!this.pagination.to){\n                    return [];\n                }\n                var from = this.pagination.current_page - this.offset;\n                if(from <1){\n                    from=1;\n                }\n\n                var to = from + (this.offset * 2);\n                if(to >= this.pagination.last_page){\n                    to = this.pagination.last_page;\n                }\n\n                var pagesArray = [];\n                while(from <= to ){\n                    pagesArray.push(from);\n                    from++;\n                }\n\n                return pagesArray;\n           }\n        },\n        methods : {\n            cambiarPagina(page,buscar,criterio){\n                let me = this;\n                me.pagination.current_page = page;\n                me.loadRegistros(page,buscar,criterio);\n            },\n            eliminar(id){\n              const swalWithBootstrapButtons = Swal.mixin({\n                  customClass: {\n                    confirmButton: 'btn btn-success',\n                    cancelButton: 'btn btn-danger'\n                  },\n                  buttonsStyling: false\n                })\n\n                swalWithBootstrapButtons.fire({\n                  title: 'Desea cambiar eliminar el registro?',\n                  icon: 'warning',\n                  showCancelButton: true,\n                  confirmButtonText: 'Aceptar',\n                  cancelButtonText: 'Cancelar',\n                  reverseButtons: true\n                }).then((result) => {\n                  if (result.value) {\n\n                    let me=this;\n                    axios.put('/superadmin/pre-registers/delete',{\n                        'id': id\n                    }).then(function (response){\n                        me.loadRegistros(me.pagination.current_page,me.buscar,me.criterio);\n                        swalWithBootstrapButtons.fire(\n                          'Inactivo!',\n                          'Eliminacin exitosa.',\n                          'success'\n                        )\n                    }).catch(function (error){\n                        console.log(error);\n                    });\n\n                  }\n                })\n            },\n            loadRegistros(page,buscar,criterio){\n                let me=this;\n                var url = '/superadmin/pre-registers/get?page='+page+'&buscar='+buscar+'&criterio='+criterio;\n                axios.get(url).then(function (response){\n                    var respuesta  = response.data;\n                    me.arrayRegistros = respuesta.registers.data;\n                    me.pagination = respuesta.pagination;\n                  })\n                  .catch(function (error) {\n                    // handle error\n                    console.log(error);\n                  })\n                  .finally(function () {\n                    // always executed\n                  });\n            },\n        },\n        mounted() {\n            this.loadRegistros(1,'','name');\n        }\n    }\n</script>\n\n<style>\n    .modal-content{\n        width: 100% !important;\n        position: absolute !important;\n    }\n    .mostrar{\n        display: list-item !important;\n        opacity: 1 !important;\n        position: fixed !important;\n        background-color: #3c29297a !important;\n        overflow: scroll;\n    }\n\n    .div-error{\n        display: flex;\n        justify-content: center;\n    }\n\n    .text-error{\n        color: red !important;\n        font-weight: bold;\n    }\n</style>\n"],"sourceRoot":""}]);
